import React, { FC, ReactElement, useState } from "react";
import { MdNotInterested, MdOutlineTimer, MdWarning } from "react-icons/md";
import { FC_CardStyle } from "../../actions";
import {
  addMinutesModal,
  continueHandler,
  SelectCandidateHandler,
  stopCandidateHandler,
  warnCandidateHandler,
} from "../../containers/ProctoringOnlineSupervisor/ProctoringOnlineSupervisor";
import { CandidateProctoringInterface } from "../../interfaces";
import { CountDown } from "../CountDown/CountDown";

type CenterCardSupervisorProps = {
  candidate: CandidateProctoringInterface;
  exam_started: boolean;
  focus_mode: boolean;
  exam_done: boolean;
  duration: number;
  warningHandler: warnCandidateHandler;
  stopHandler: stopCandidateHandler;
  detailsHandler: SelectCandidateHandler;
  continueHandler: continueHandler;
  addMinutes: addMinutesModal;
};

const CenterCardSupervisor: FC<CenterCardSupervisorProps> = ({
  candidate,
  warningHandler,
  stopHandler,
  detailsHandler,
  continueHandler,
  exam_started,
  focus_mode,
  exam_done,
  addMinutes,
  // duration,
}): ReactElement => {
  // let color = candidate?.stopped === true ? "red" : focus_mode === true ? "blue" : "gray";
  const [candidate_done, setCandidate_done] = useState(true);
  return (
    <div
      // className={`bg-${color}-900 bg-opacity-50 hover:bg-opacity-100 border-${color}-400 border text-white    cursor-pointer  p-2 rounded-md transition-all ease-in-out`}
      className={`${FC_CardStyle(
        candidate?.stopped,
        focus_mode,
        candidate.done
      )} text-white    cursor-pointer  p-2 rounded-md transition-all ease-in-out`}
      onClick={detailsHandler.bind(this, candidate.user_id)}
    >
      <div className="text-center">
        <span className={`text-gray-200`}>Seat number</span>
        <h1 className="text-5xl font-bold">{candidate.seat_number}</h1>
      </div>

      {candidate.added_minutes !== 0 && (
        <div className="flex items-center text-sm">
          <div className="">
            <section className="p-0.5 bg-blue-500 rounded w-">
              {candidate.added_minutes}m
            </section>
          </div>
          <div className="flex-1"></div>
          <div className=" pl-2 font-bold text-center">
            {exam_done === true && (
              <section className="p-0.5 bg-blue-500 rounded w-auto px-1">
                <CountDown
                  start_time={candidate.start_added_time}
                  duration_min={candidate.added_minutes}
                  timeout={() => setCandidate_done(false)}
                  returnString={true}
                  labels
                />
              </section>
            )}
          </div>
        </div>
      )}

      <div className={`mt-1 border-t border-gray-200 pt-2`}>
        <h4 className={`text-xs text-gray-200`}>Full names</h4>
        <h4 className="text-sm font-bold">
          {candidate.fname} {candidate.lname}
        </h4>
      </div>

      {((exam_started === true && exam_done === false) ||
        (candidate.added_minutes !== 0 && candidate_done !== false)) && (
        <div className="mt-2 flex">
          {candidate.stopped === false ? (
            <button
              onClick={warningHandler.bind(this, candidate.user_id)}
              className="px-2 text-sm py-1 rounded-md text-gray-900 bg-yellow-300 flex-1 border border-white"
              title="Send a warning"
            >
              <MdWarning className="inline" /> Warn
            </button>
          ) : (
            <button
              onClick={continueHandler.bind(this, candidate.user_id)}
              className="px-2 text-sm py-1 rounded-md text-gray-900 bg-green-400 flex-1"
              title="Allow to continue exam"
            >
              <MdWarning className="inline" /> Continue
            </button>
          )}
          <button
            onClick={stopHandler.bind(this, candidate.user_id)}
            className="px-2 text-sm py-1 rounded-md text-white- bg-red-700 ml-1 flex-1"
            title="Stop candidate"
          >
            <MdNotInterested className="inline" /> Stop
          </button>
        </div>
      )}

      {(exam_done === true || exam_started === true) && (
        <div className="mr-2 mt-2">
          <button
            onClick={() => {
              addMinutes(candidate);
              setCandidate_done(true);
            }}
            className="px-2 py-1 rounded-md text-sm w-full bg-blue-700 ml-1"
            title="Add minutes"
          >
            <MdOutlineTimer className="inline" /> Add minutes
          </button>{" "}
        </div>
      )}
      {/* {exam_done === true &&
        candidate.added_minutes !== 0 &&
        candidate.stopped !== true && <div> Time out dom </div>} */}
    </div>
  );
};
export default CenterCardSupervisor;
