import React, { FC, ReactElement } from "react";
import { MdMic, MdMicOff, MdVolumeOff, MdVolumeUp } from "react-icons/md";
type SoundControlProps = {
  sound_text: string;
  speaker_text: string;
  speakToAll: () => void;
  stopSpeaking: () => void;
  listenAll: () => void;
  stopListening: () => void;
};

const Button: FC<{
  icon: any;
  title: string;
  active: boolean;
  accent: string;
  click: () => void;
  disabled: boolean;
  description: string;
}> = ({
  title,
  icon,
  active,
  accent,
  click,
  disabled,
  description,
}): ReactElement => {
  const Icon = icon;
  return (
    <button
      onClick={click}
      disabled={disabled}
      className={`flex text-xs items-center p-1 mx-0.5 px-2 rounded-full ${
        active === true ? `bg-${accent}-600 text-white` : ""
      } border border-${accent}-300 hover:bg-${accent}-700 hover:text-white text-${accent}-300`}
      title={description}
    >
      <Icon className="text-xl " />
      <span className="opacity-80">{title}</span>
    </button>
  );
};

const SoundControl: FC<SoundControlProps> = ({
  sound_text,
  speaker_text,
  speakToAll,
  stopSpeaking,
  listenAll,
  stopListening,
}): ReactElement => {
  /* function body */
  return (
    <div className="flex p-1 bg-blue-900 mb-2 sticky bg-opacity-50 border-b border-blue-500">
      <div className="flex-1">
        <h6 className="text-xs text-blue-200">
          Audio{" "}
          <span className="font-light opacity-40">(what they listen)</span>
        </h6>
        <div className="mt-1 flex items-center">
          <Button
            title="Speak to all"
            description="Speak to all candidates"
            accent="green"
            icon={MdMic}
            click={speakToAll}
            active={false}
            disabled={false}
          />
          <Button
            title=""
            description="Stop speaking to all candidates"
            accent="red"
            icon={MdMicOff}
            click={stopSpeaking}
            active={false}
            disabled={false}
          />

          <p className="px-1 text-white">{sound_text}</p>
        </div>
      </div>
      <div className="flex-1 text-right">
        <h6 className="text-xs text-blue-200">
          Speaker{" "}
          <span className="font-light opacity-40">(what you listen)</span>
        </h6>
        <div className="mt-1 flex items-center flex-row-reverse">
          <Button
            title="listen all"
            description="listen all candidates"
            accent="green"
            icon={MdVolumeUp}
            click={listenAll}
            active={false}
            disabled={false}
          />
          <Button
            title=""
            description="Stop listening to all candidate"
            accent="red"
            icon={MdVolumeOff}
            click={stopListening}
            active={false}
            disabled={false}
          />
          <p className="px-1 text-white">{speaker_text}</p>
        </div>
      </div>
    </div>
  );
};
export default SoundControl;
