import React, { FC, ReactElement, useEffect } from "react";
import {
  MdOutlineMic,
  MdVolumeUp,
  MdOutlineCenterFocusStrong,
  MdBackHand,
  MdMicOff,
  MdVolumeOff,
} from "react-icons/md";
import { CandidateProctoringInterface } from "../../interfaces";
import { shortString } from "../../utils/functions";
import HoverControls from "./HoverControls";

const styles_videoElement: React.CSSProperties = {
  width: "100%",
  height: "10rem",
  minWidth: "100%",
  minHeight: "100%",
  objectFit: "cover",
  objectPosition: "top",
};

type LocalVideoCallProps = {
  localStream: any;
  candidate: CandidateProctoringInterface;
  micAdd: (candidate_id: string) => void;
  micStop: (candidate_id: string) => void;
  micOnly: (candidate_id: string) => void;
  soundAdd: (candidate_id: string) => void;
  soundStop: (candidate_id: string) => void;
  soundOnly: (candidate_id: string) => void;
  open: (candidate_id: string) => void;
  focus: (candidate_id: string) => void;
};

const LocalVideoCall: FC<LocalVideoCallProps> = ({
  localStream,
  candidate,
  micAdd,
  micStop,
  micOnly,
  soundAdd,
  soundStop,
  soundOnly,
  open,
  focus,
}): ReactElement => {
  const localVideoRef = React.useRef<HTMLVideoElement>(null);

  useEffect(() => {
    if (localStream) {
      const localVideo = localVideoRef.current;

      if (localVideo !== null) {
        localVideo.srcObject = localStream;
        localVideo.onloadedmetadata = () => {
          localVideo.play();
        };
      }
    }
  }, [localStream]);

  let user_id = candidate.user_id;
  let username = shortString(candidate.lname, 11);

  return (
    <div
      className={`group  border-2 ${
        candidate.stopped === true
          ? "border-red-300 bg-red-300"
          : "border-blue-400 bg-blue-400"
      }  h-52 relative rounded-md`}
      title={`${candidate.fname} ${candidate.lname}`}
    >
      <video
        style={styles_videoElement}
        className="w-full  h-52 z-10"
        ref={localVideoRef}
        autoPlay
        muted={!candidate.audio}
      />

      {candidate.hand === true && (
        <div className="absolute top-0 left-0 z-50 bg-black bg-opacity-40 rounded-tl-sm rounded-br-2xl p-1">
          <MdBackHand className="text-2xl text-yellow-300" stroke="red" />
        </div>
      )}

      <div className="absolute top-1 right-1 z-50 ">
        <button
          onClick={focus.bind(this, user_id)}
          title="Focus on user"
          className="inline-block text-xl ml-1 p-0.5 text-white bg-black bg-opacity-60 rounded-lg  hover:bg-blue-800"
        >
          <MdOutlineCenterFocusStrong />
        </button>
      </div>

      <div className="w-full flex items-center px-1 -mt-10 absolute">
        <div className="flex-1">
          <button
            onClick={open.bind(this, user_id)}
            className="p-0.5 px-2 rounded-xl bg-black bg-opacity-50 inline-block text-white hover:bg-blue-800"
          >
            <h2 className="font-bold">{username}</h2>
          </button>
        </div>

        <div className="text-right">
          <div className="inline-block hover-parent z-50">
            <button
              className={
                "inline-block text-2xl ml-1 p-1 text-white bg-black bg-opacity-60 rounded-lg hover:bg-blue-900"
              }
            >
              {candidate.mic === true ? (
                <MdOutlineMic className="text-red-400" />
              ) : (
                <MdMicOff className="text-gray-100" />
              )}
            </button>

            <HoverControls
              add={micAdd.bind(this, user_id)}
              stop={micStop.bind(this, user_id)}
              only={micOnly.bind(this, user_id)}
              icon={candidate.mic === true ? MdOutlineMic : MdMicOff}
              name={username}
              sub_title={"speakers"}
              title={"speak"}
            />
          </div>

          <div className="inline-block hover-parent z-50">
            <button className="inline-block text-2xl ml-1 p-1 text-white bg-black bg-opacity-60 rounded-lg  hover:bg-blue-900">
              {candidate.audio === true ? (
                <MdVolumeUp className="text-green-400" />
              ) : (
                <MdVolumeOff className="text-gray-100" />
              )}
            </button>

            <HoverControls
              add={soundAdd.bind(this, user_id)}
              stop={soundStop.bind(this, user_id)}
              only={soundOnly.bind(this, user_id)}
              icon={candidate.audio === true ? MdVolumeUp : MdVolumeOff}
              name={username}
              sub_title={"listeners"}
              title={"listen"}
            />
          </div>
        </div>
      </div>
    </div>
  );
};
export default LocalVideoCall;
