import moment from "moment";
import React, { Fragment } from "react";
import {
  MdAddCircle,
  MdError,
  MdInfoOutline,
  MdKeyboardBackspace,
} from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  Proctoring,
  createCandidateRoomAPI,
  FT_CloseProctoringRoom,
  FC_CloseProctoringRoom,
  FC_ClearProctoring,
  FT_ClearProctoring,
} from "../../actions";
import { FT_createAttendance } from "../../containers/ProctoringOnlineSupervisor/ProctoringOnlineSupervisor";
// import { EditProfileDetails } from "../../containers/ProfileDetails/EditProfileDetails";
import { CandidateProctoringInterfaceAPI } from "../../interfaces";
import { StoreState } from "../../reducers";
import search from "../../utils/search";
import Modal, { ModalSize, Themes } from "../Modal/Modal";

// props for the component
interface AppProps {
  auth: Auth;
  proctoring: Proctoring;
  createAttendance: FT_createAttendance;
  FC_CloseProctoringRoom: FT_CloseProctoringRoom;
  FC_ClearProctoring: FT_ClearProctoring;
}

interface AppState {
  loading: boolean;
  create_attendance: boolean;
  search: string;
  selected_list: string[];
  selected_candidate: CandidateProctoringInterfaceAPI | null;
  group_name: string;
  error: {
    target: string;
    msg: string;
  };
  created_groups: string[];
  candidates_in_groups: CandidateProctoringInterfaceAPI[];
  candidate_non_done_exam: CandidateProctoringInterfaceAPI[];
  disabling_room: boolean;
  group_list: string[];
}

class _Attendance extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      create_attendance: false,
      selected_list: [],
      selected_candidate: null,
      search: "",
      group_name: "",
      error: {
        target: "",
        msg: "",
      },
      created_groups: [],
      candidates_in_groups: [],
      candidate_non_done_exam: [],
      disabling_room: false,
      group_list: [],
    };
  }

  componentDidMount = () => {
    let group_list: string[] = [];
    let done_candidates: {
      user_id: string;
      seat_number: number;
      group_id: string;
    }[] = [];
    let candidates_in_with_groups: CandidateProctoringInterfaceAPI[] = [];
    // let candidate_non_done_exam: CandidateProctoringInterfaceAPI[] = [];

    this.props.proctoring.room_groups?.forEach((group) => {
      group_list.push(group.group_id.toLowerCase());
      done_candidates = [
        ...done_candidates,
        ...group.candidates.map((x) => ({ ...x, group_id: group.group_id })),
      ];
    });

    candidates_in_with_groups = this.props.proctoring.all_candidates.map(
      (itm) => {
        // check if the candidate exists
        let candidate_exist = done_candidates.find(
          (el) => el.user_id === itm.user_id
        );
        if (candidate_exist === undefined) {
          return { ...itm, group_id: "" };
        } else {
          return { ...itm, group_id: candidate_exist.group_id };
        }
      }
    );

    this.setState({
      candidates_in_groups: candidates_in_with_groups.filter(
        (cand) => cand.group_id !== ""
      ),
      candidate_non_done_exam: candidates_in_with_groups.filter(
        (cand) => cand.group_id === ""
      ),
      group_list: group_list,
    });
  };

  submitHandler = (e: any) => {
    e.preventDefault();

    if (this.state.group_name.length === 0) {
      return this.setState({
        error: {
          target: "form",
          msg: "Please add the group name",
        },
      });
    }

    if (this.state.selected_list.length === 0) {
      return this.setState({
        error: {
          target: "form",
          msg: "You can't create an empty group",
        },
      });
    }

    if (
      this.state.group_list.includes(this.state.group_name.toLowerCase()) ===
      true
    ) {
      return this.setState({
        error: {
          target: "form",
          msg: "This name already used on previous group",
        },
      });
    }

    this.props.createAttendance(
      {
        list: this.state.selected_list,
        name: this.state.group_name,
      },
      true
    );
  };

  FC_CloseProctoringRoomHandler = (id: string | undefined) => {
    if (id === undefined) return;
    if (
      window.confirm(
        "Are you sure you want to set this room to finished status??"
      ) === false
    )
      return;

    this.setState({ disabling_room: true });
    this.props.FC_CloseProctoringRoom(id, [], (status, msg) => {
      if (status === false) {
        this.setState({
          error: { target: "main", msg: msg },
          disabling_room: false,
        });
      } else {
        this.setState({ disabling_room: false });
      }
    });
  };

  render() {
    let expiredRooms: createCandidateRoomAPI[] = [];

    // check if there is a room which is not yet closed
    if (this.props.proctoring.room_groups !== null) {
      expiredRooms = this.props.proctoring.room_groups?.filter(
        (data) =>
          data.isExpired === false &&
          data.exam_id === this.props.proctoring.exam.exam_id
      );
    }

    if (expiredRooms.length >= 1) {
      return (
        <div className="p-4 bg-white rounded mt-3 mr-2">
          <button
            className="py-1 px-2 bg-blue-300 rounded-md text-white"
            onClick={() => this.props.FC_ClearProctoring()}
          >
            back
          </button>
          <div className=" p-2 text-center">
            <h1 className="text-4xl">Some previous room are not closed yet</h1>
            <h3 className="text-lg text-gray-700">
              If you want to create another room, please close all the rooms
              first
            </h3>
          </div>

          {this.state.disabling_room === true && (
            <div className="my-2 bg-blue-500 text-white px-2 text-center rounded animate__animated animate__fadeIn animate_infinite cursor-pointer">
              Updating groups...
            </div>
          )}

          {this.state.error.target === "main" && (
            <div
              className="my-2 bg-red-500 text-white px-2 text-center rounded animate__animated animate__zoomIn cursor-pointer"
              onClick={() => this.setState({ error: { target: "", msg: "" } })}
            >
              {this.state.error.msg}
            </div>
          )}

          <div className="mt-3">
            <table className="w-full table table-auto">
              <thead className="text-left bg-gray-100 text-gray-600">
                <tr>
                  <th>#</th>
                  <th>Group name</th>
                  <th>Created time</th>
                  <th>Total candidates</th>
                  <th>Course</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {expiredRooms.map((itm, i) => (
                  <tr key={itm._id} className="border-b">
                    <td className="p-3">{i + 1}</td>
                    <td>
                      <b>{itm.group_id}</b>
                    </td>
                    <td>{moment(itm.createdAt).format("LLLL")}</td>
                    <td>{itm.candidates.length}</td>
                    <td>{this.props.proctoring.exam.course_name}</td>
                    <td className="text-right">
                      <button
                        onClick={(e) => {
                          if (
                            window.confirm(
                              "Are you sure you want to re0create the group?? this is only used once some candidates have not done the exam"
                            )
                          ) {
                            this.props.createAttendance(
                              {
                                list: itm.candidates.map((itm) => itm.user_id),
                                name: itm.group_id,
                              },
                              false
                            );
                          }
                        }}
                        className="p-1 rounded bg-blue-400 mr-2 text-white px-3"
                      >
                        Backup <b>{itm.group_id}</b> group
                      </button>
                      <button
                        onClick={this.FC_CloseProctoringRoomHandler.bind(
                          this,
                          itm._id
                        )}
                        className="p-1 rounded bg-red-400 text-white px-3"
                      >
                        Close <b>{itm.group_id}</b> group
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      );
    }

    let resultCandidates: CandidateProctoringInterfaceAPI[] = search(
      this.state.candidate_non_done_exam,
      this.state.search,
      {
        email: true,
        fname: true,
        lname: true,
      }
    );

    if (
      this.props.proctoring.candidates.length === 0 &&
      this.state.create_attendance === false
    ) {
      return (
        <div className="mt-4 mx-4">
          {this.props.proctoring.error !== "" && (
            <div className="p-2 mb-3 bg-yellow-300 animate__zoomIn animate__faster animate__animated text-black rounded-md flex items-center">
              <MdError className="text-2xl" />
              <p className="m-0 text-xl ml-1.5">
                {this.props.proctoring.error}
              </p>
            </div>
          )}

          <div className="p-6 bg-white animate__fadeIn animate__animated mt-4 rounded shadow-md">
            <h2 className="text-3xl font-bold mb-2">Create a group</h2>
            <p className="text-md text-gray-700 ">
              To create a group click on <b>create group</b> button and check
              list of all the candidates and add them to the attendance with the
              room name
            </p>

            <div className="my-2 border-- w-4/5 py-2">
              {[
                {
                  title: "Province",
                  value: this.props.proctoring.room.province_name,
                },
                {
                  title: "District",
                  value: this.props.proctoring.room.district_name,
                },
                {
                  title: "Center name",
                  value: this.props.proctoring.room.center_name,
                },
                {
                  title: "Room number",
                  value: this.props.proctoring.room.room_number,
                },
              ].map((itm) => (
                <section key={itm.title} className="border-t py-1">
                  <span>{itm.title}: </span>
                  <b>{itm.value}</b>
                </section>
              ))}
            </div>

            <div className="">
              <button
                className="p-2 px-3 rounded text-lg mt-2 bg-red-500 text-white hover:bg-600"
                onClick={() => this.props.FC_ClearProctoring()}
              >
                Cancel
              </button>{" "}
              <button
                className="p-2 px-3 rounded text-lg mt-2 bg-blue-500 text-white hover:bg-600"
                onClick={() => this.setState({ create_attendance: true })}
              >
                Create group
              </button>
            </div>
          </div>
        </div>
      );
    }

    return (
      <Fragment>
        {this.state.selected_candidate !== null && (
          <Modal
            footer={false}
            widthSizeClass={ModalSize.large}
            title="User info"
            theme={Themes.default}
            displayClose={true}
            padding={{ title: true, body: true, footer: false }}
            backDrop={true}
            backDropClose={true}
            close={() => this.setState({ selected_candidate: null })}
          >
            <div className="p-2">
              {[
                {
                  title: "National Id",
                  value: this.state.selected_candidate.n_id,
                },
                {
                  title: "First name",
                  value: this.state.selected_candidate.fname,
                },
                {
                  title: "Last name",
                  value: this.state.selected_candidate.lname,
                },
                {
                  title: "District",
                  value: this.state.selected_candidate.district,
                },
                {
                  title: "Phone number",
                  value: this.state.selected_candidate.phone_number,
                },
                {
                  title: "Course",
                  value:
                    this.state.selected_candidate.course_code +
                    " " +
                    this.state.selected_candidate.course_name,
                },
                { title: "Email", value: this.state.selected_candidate.email },
                {
                  title: "Position",
                  value: this.state.selected_candidate.position_title,
                },
              ].map((itm) => (
                <section key={itm.title} className="border-t py-1">
                  <span>{itm.title}: </span>
                  <b>{itm.value}</b>
                </section>
              ))}
            </div>
          </Modal>
        )}

        <form onSubmit={this.submitHandler}>
          <div className="bg-white p-2 mr-2 mt-3 rounded-md animate__animated animate__fadeIn">
            <div className=" px-2 pb-3 flex items-center">
              <div className="flex-1">
                <div className="flex">
                  <button
                    className="text-sm mr-2 hover:text-white hover:bg-blue-600 rounded-md px-1 text-blue-500"
                    onClick={() => this.props.FC_ClearProctoring()}
                  >
                    <MdKeyboardBackspace className="inline-block " /> Back
                  </button>
                  <h2 className="font-bold text-xl">Create a group</h2>
                </div>
                <p className="text-gray-400 m-0 mt- text-sm">
                  Please create a room by adding the list of the users to the
                  attendee list
                </p>
              </div>
              <div className="">
                <div className="flex items-end">
                  <div className="">
                    <label htmlFor="input-group-name" className="block text-xs">
                      Group name
                    </label>
                    <input
                      type="text"
                      id="input-group-name"
                      className={`p-1 px-2 bg-gray-50 rounded-md ${
                        this.state.error.target === "form"
                          ? "border-red-600"
                          : ""
                      }  border`}
                      placeholder="A"
                      maxLength={2}
                      required
                      value={this.state.group_name}
                      onChange={(e) =>
                        this.setState({
                          group_name: e.target.value.toUpperCase(),
                        })
                      }
                    />
                  </div>
                  <div className="flex-1 pl-1">
                    <button
                      type="submit"
                      className="py-1 px-3 text-md hover:bg-blue-600 rounded-md bg-blue-500 text-white block"
                    >
                      Create group
                    </button>
                  </div>
                </div>
                {this.state.error.target === "form" && (
                  <span className="text-red-500 text-sm">
                    {this.state.error.msg}
                  </span>
                )}
              </div>
            </div>
            <div className="md:flex">
              {this.state.selected_list.length !== 0 &&
              this.state.selected_list.length ===
                this.props.proctoring.all_candidates.length ? null : (
                <div className="flex-1 mr-2 mb-8 md:mb-2">
                  <div className="p-2 flex bg-gray-200 items-center">
                    <h4 className="text-xl font-light text-gray-700">
                      All candidates
                    </h4>
                    <input
                      type="search"
                      name="search"
                      id=""
                      placeholder="search name"
                      className="py-1 px-2 flex-1 ml-3 bg-gray-50 border rounded"
                      value={this.state.search}
                      onChange={(e) =>
                        this.setState({ search: e.target.value })
                      }
                    />
                  </div>
                  <div className=" shadow">
                    <table className="w-full">
                      <thead className="text-left bg-gray-400">
                        <tr>
                          <th className="text-center">#</th>
                          <th>Names</th>
                          <th>Email</th>
                          <th className="text-right">
                            <button
                              type="button"
                              className="p-0.5 px-2 rounded-md hover:bg-blue-700 m-1 inline-block bg-blue-600 text-white text-sm"
                              onClick={() =>
                                this.setState({
                                  selected_list:
                                    this.state.candidate_non_done_exam.map(
                                      (itm) => itm.user_id
                                    ),
                                })
                              }
                            >
                              Select all
                            </button>
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        {resultCandidates.length === 0 ? (
                          <tr>
                            <td colSpan={4}>
                              <div className="p-2 text-center py-4">
                                <MdInfoOutline className="text-6xl text-gray-500 mx-auto my-1" />
                                <h2>
                                  <b>{this.state.search}</b> can't be found in
                                  waiting candidates
                                </h2>
                              </div>
                            </td>
                          </tr>
                        ) : (
                          <>
                            {resultCandidates.map(
                              (itm: CandidateProctoringInterfaceAPI, i) =>
                                this.state.selected_list.indexOf(itm.user_id) >
                                -1 ? null : (
                                  <tr
                                    key={i}
                                    className="border-b group hover:bg-blue-50"
                                  >
                                    <td className="text-gray-400 px-1 text-sm text-center">
                                      {" "}
                                      {i + 1}
                                    </td>
                                    <td
                                      title="Click to see details"
                                      className="font-bold text-blue-900 hover:underline cursor-pointer"
                                      onClick={() =>
                                        this.setState({
                                          selected_candidate: itm,
                                        })
                                      }
                                    >
                                      {itm.fname} {itm.lname}
                                    </td>
                                    <td className="text-sm">{itm.email}</td>
                                    <td className="text-right">
                                      <button
                                        type="button"
                                        onClick={() =>
                                          this.setState({
                                            selected_list: [
                                              ...this.state.selected_list,
                                              itm.user_id,
                                            ],
                                            error: {
                                              target: "",
                                              msg: "",
                                            },
                                          })
                                        }
                                        className="my-1 p-1 px-2 rounded-md group-hover:opacity-95 opacity-5 bg-blue-500 text-white text-sm"
                                      >
                                        <MdAddCircle className="inline-block mr-1" />
                                        Add
                                      </button>
                                    </td>
                                  </tr>
                                )
                            )}

                            {this.state.search === "" && (
                              <>
                                <tr>
                                  <td colSpan={5}>
                                    <h4 className="text-center text-2xl text-gray-400 p-2 border-b">
                                      Candidates who are done the exam
                                    </h4>
                                  </td>
                                </tr>
                                {this.state.candidates_in_groups.map(
                                  (itm: CandidateProctoringInterfaceAPI, i) =>
                                    this.state.selected_list.indexOf(
                                      itm.user_id
                                    ) > -1 ? null : (
                                      <tr
                                        key={i}
                                        className="border-b group  opacity-70"
                                      >
                                        <td className="text-gray-400 px-1 text-sm text-center">
                                          {" "}
                                          {i + 1}
                                        </td>
                                        <td
                                          title="Click to see details"
                                          className="font-bold text-blue-900 hover:underline cursor-pointer"
                                          onClick={() =>
                                            this.setState({
                                              selected_candidate: itm,
                                            })
                                          }
                                        >
                                          {itm.fname} {itm.lname}
                                        </td>
                                        <td className="text-sm">{itm.email}</td>
                                        <td className="">
                                          Group: <b>{itm.group_id}</b>
                                        </td>
                                      </tr>
                                    )
                                )}
                              </>
                            )}
                          </>
                        )}
                      </tbody>
                    </table>
                  </div>
                </div>
              )}
              <div className="flex-1 ml-1 shadow border">
                <div className="p-1 bg-blue-100 py-1 px-2 flex items-center">
                  <div className="flex-1">
                    <h2 className="font-bold text-blue-900 text-2xl">Group</h2>
                    <p className="text-blue-800 m-0 mt- text-sm">
                      This is the list of the candidate who are allowed to do
                      the exam
                    </p>
                  </div>
                  <div className="">
                    <h2 className="text-xl font-bold -mb-2">
                      <b>{this.state.selected_list.length}</b> of{" "}
                      <b>{this.props.proctoring.all_candidates.length}</b>
                    </h2>
                    <p className="text-sm">Selected</p>
                  </div>
                </div>
                <div className="">
                  <table className="w-full">
                    <thead className="text-left bg-blue-800 text-white">
                      <tr>
                        <th className="px-2">#</th>
                        <th>Names</th>
                        <th>Email</th>
                        <th className="text-right">
                          {this.state.selected_list.length >= 2 && (
                            <button
                              type="button"
                              className="p-0.5 px-2 rounded-md hover:bg-red-700 hover:text-red-100 m-1 inline-block bg-red-200 text-red-800 text-sm"
                              onClick={() =>
                                this.setState({
                                  selected_list: [],
                                })
                              }
                            >
                              Remove all
                            </button>
                          )}
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.selected_list.length === 0 ? (
                        <tr>
                          <td colSpan={4}>
                            <div className="p-2 text-center py-4">
                              <MdInfoOutline className="text-6xl text-gray-500 mx-auto my-1" />
                              <h2>No candidate found on the attendance</h2>
                            </div>
                          </td>
                        </tr>
                      ) : (
                        this.state.selected_list.map(
                          (user_id: string, i: number) => {
                            let candidateFind =
                              this.props.proctoring.all_candidates.find(
                                (cand) => cand.user_id === user_id
                              );
                            if (candidateFind === undefined) {
                              return null;
                            }
                            return (
                              <tr
                                key={i}
                                className="border-b group hover:bg-blue-50 animate__animated animate__zoomIn animate__faster"
                              >
                                <td className="text-gray-400 pr-1 text-center">
                                  {i + 1}
                                </td>
                                <td
                                  title="Click to see details"
                                  className="font-bold text-blue-900 hover:underline cursor-pointer"
                                  onClick={() =>
                                    candidateFind !== undefined &&
                                    this.setState({
                                      selected_candidate: candidateFind,
                                    })
                                  }
                                >
                                  {candidateFind.fname} {candidateFind.lname}
                                </td>
                                <td>{candidateFind.email}</td>
                                <td className="text-right">
                                  <button
                                    type="button"
                                    onClick={() =>
                                      this.setState({
                                        selected_list:
                                          this.state.selected_list.filter(
                                            (el) => el !== user_id
                                          ),
                                      })
                                    }
                                    className="my-1 p-1 px-2 rounded-md group-hover:opacity-95 opacity-5 bg-red-500 text-white text-sm"
                                  >
                                    Remove
                                  </button>
                                </td>
                              </tr>
                            );
                          }
                        )
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </form>
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  proctoring,
}: StoreState): { auth: Auth; proctoring: Proctoring } => {
  return { auth: auth, proctoring: proctoring };
};

export const Attendance = connect(mapStateToProps, {
  FC_CloseProctoringRoom,
  FC_ClearProctoring,
})(_Attendance);
