// import moment, { duration } from "moment";
import moment, { duration } from "moment-timezone";

import React from "react";
import { connect } from "react-redux";
import { Auth, Proctoring, Socket } from "../../actions";
import { EventInterface, Events, StartExam_Callback } from "../../interfaces";
import { StoreState } from "../../reducers";
import { TimeZone } from "../../utils/app.config";

moment.tz.add("Kigali/Africa|PST PDT|80 70|0101|1Lzm0 1zb0 Op0");

// props for the component
interface AppProps {
  auth: Auth;
  proctoring: Proctoring;
  room_id: string;
  timeout: Function;

  returnString?: boolean;
  separator?: string;
  labels?: boolean;
  days?: boolean;
  socket: Socket;
}

interface AppState {
  loading: boolean;
  days: number;
  hours: number;
  mins: number;
  secs: number;
  start_time: Date | null;
  duration: number;
  stop_time: any;
  error: boolean;
}

class _CountDown extends React.Component<AppProps, AppState> {
  interval: any;
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      days: 0,
      hours: 0,
      mins: 0,
      secs: 0,
      start_time: null,
      duration: 0,
      stop_time: null,
      error: false,
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.startOnlineTimer();
    }, 1000);

    // * CONTINUE EXAM
    this.props.socket.socket.on(
      Events.ADD_TIME_TO_CANDIDATE_CLIENT,
      (data: any, event: EventInterface) => {
        if (event.user_id === this.props.auth.user.user_id) {
          this.startOnlineTimer();
        }
      }
    );
  }

  startOnlineTimer() {
    this.props.socket.socket.emit(
      Events.GET_ROOM_TIME,
      {
        user_id: this.props.auth.user.user_id,
        room_id: this.props.room_id,
      },
      (res: StartExam_Callback | null) => {
        if (res === null) return this.setState({ error: true });
        console.log({
          TimerCountDownResult: res,
        });

        // compute the dates and start running the functions
        if (res.remaining_minutes === 0) {
          return this.setState({
            days: 0,
            hours: 0,
            mins: 0,
            secs: 0,
          });
        } else {
          let examStopTime = moment().add(res.remaining_minutes, "minutes");

          console.log({
            examStopTime,
          });

          this.setState({
            stop_time: examStopTime,
          });
          this.setCountdowm();
          this.timeOutCounter();

          // Count down
          //   this.setCountdowmDates(examStopTime);
        }
      }
    );
  }

  timeOutCounter = () => {
    this.interval = setInterval(() => {
      this.setCountdowm();
      if (this.state.hours < 0 || this.state.mins < 0 || this.state.secs < 0) {
        this.props.timeout();
        clearInterval(this.interval);
        this.setState({
          days: 0,
          hours: 0,
          mins: 0,
          secs: 0,
        });
      }
    }, 1000);
  };

  //   componentDidUpdate(prevProps: AppProps, prevState: AppState): void {
  //     if (
  //       prevProps.duration_min !== this.props.duration_min ||
  //       prevProps.start_time !== this.props.start_time
  //     ) {
  //       clearInterval(this.interval);
  //       this.setState({
  //         duration: this.props.duration_min,
  //         start_time: this.props.start_time,
  //       });
  //       this.timeOutCounter();
  //     }
  //   }

  componentWillUnmount() {
    clearInterval(this.interval);

    // this.props.socket.socket.off(Events.GET_ROOM_TIME);
  }

  formatStyle(value: number): string {
    return value <= 9 ? `0${value}` : value + "";
  }

  setCountdowm() {
    const futureDate = moment(this.state.stop_time);

    const today = moment();
    const clockDuration = duration(futureDate.diff(today));

    const days = Math.floor(clockDuration.asDays());
    const hours = clockDuration.hours();
    const mins = clockDuration.minutes();
    const secs = clockDuration.seconds();

    this.setState({ days, hours, mins, secs });
  }

  render() {
    if (this.state.error === true) return null;
    if (
      this.props.returnString === undefined ||
      this.props.returnString === false
    ) {
      return (
        <div className="flex items-center">
          {this.props.days !== undefined && this.props.days !== false && (
            <div className="m-0 p-0">
              {this.props.labels !== undefined &&
                this.props.labels !== false && (
                  <span className="m-0 text-xs opacity-80">Days</span>
                )}
              <p className="m-0  timer-count-down -mt-2">
                {this.formatStyle(this.state.days)}
                {this.props.separator === undefined
                  ? ":"
                  : this.props.separator}
              </p>
            </div>
          )}
          <div>
            {this.props.labels !== undefined && this.props.labels !== false && (
              <span className="m-0 text-xs opacity-80">Hours</span>
            )}
            <p className="m-0  timer-count-down -mt-2">
              {this.formatStyle(this.state.hours)}
              {this.props.separator === undefined ? ":" : this.props.separator}
            </p>
          </div>

          <div>
            {this.props.labels !== undefined && this.props.labels !== false && (
              <span className="m-0 text-xs opacity-80">Min</span>
            )}
            <p className="m-0  timer-count-down -mt-2">
              {this.formatStyle(this.state.mins)}
              {this.props.separator === undefined ? ":" : this.props.separator}
            </p>
          </div>

          <div>
            {this.props.labels !== undefined && this.props.labels !== false && (
              <span className="m-0 text-xs opacity-80">Sec</span>
            )}
            <p className="m-0  timer-count-down -mt-2">
              {this.formatStyle(this.state.secs)}
            </p>
          </div>
        </div>
      );
    } else {
      return (
        <span className="timer-count-down">
          {(this.props.days !== undefined || this.props.days !== false) && (
            <span>
              {this.formatStyle(this.state.days)}
              {this.props.separator === undefined ? ":" : this.props.separator}
            </span>
          )}
          <span>{this.formatStyle(this.state.hours)}</span>
          {this.props.separator === undefined ? ":" : this.props.separator}
          <span>{this.formatStyle(this.state.mins)}</span>
          {this.props.separator === undefined ? ":" : this.props.separator}
          <span>{this.formatStyle(this.state.secs)}</span>
        </span>
      );
    }
  }
}

const mapStateToProps = ({
  auth,
  socket,
  proctoring,
}: StoreState): { auth: Auth; socket: Socket; proctoring: Proctoring } => {
  return { auth: auth, socket: socket, proctoring: proctoring };
};

export const CountDownLive = connect(mapStateToProps, {})(_CountDown);
