import moment from "moment";
import React, { Fragment } from "react";
import { FaSchool } from "react-icons/fa";
import { MdLocationOn, MdSignalWifiConnectedNoInternet4 } from "react-icons/md";
import { connect } from "react-redux";
import {
  Auth,
  FC_RoomDetails,
  Proctoring,
  FC_ExamsDetailsData,
  FC_LoadExamToRoomProctoring,
  FC_ClearExamToRoomProctoring,
  FT_AddRules,
  FC_AddRules,
  FT_LoadExamToRoomProctoring,
  FT_ExamsDetailsData,
  FT_RoomDetails,
  FT_ClearExamToRoomProctoring,
  ExamsListDates,
} from "../../actions";
import Alert, { AlertInterface } from "../../components/Alert/Alert";
import {
  ProctoringExam,
  ProctoringRoomExam,
} from "../../interfaces/socketInterfaces";
import { StoreState } from "../../reducers";
import { isToday } from "../../utils/functions";
import search from "../../utils/search";
import { ProctoringOnlineSupervisor } from "../ProctoringOnlineSupervisor/ProctoringOnlineSupervisor";

// props for the component
interface AppProps {
  auth: Auth;
  proctoring: Proctoring;
  FC_RoomDetails: FT_RoomDetails;
  FC_ExamsDetailsData: FT_ExamsDetailsData;
  FC_LoadExamToRoomProctoring: FT_LoadExamToRoomProctoring;
  FC_ClearExamToRoomProctoring: FT_ClearExamToRoomProctoring;
  FC_AddRules: FT_AddRules;
}

interface AppState {
  error: boolean;
  errorMsg: string;
  exams_error: boolean;
  exams_error_msg: string;
  search_text: string;
  loading_room_details: boolean;
  get_exams_list: boolean;
  warning: {
    target: "main-error" | null;
    msg: string;
  };
}

interface UpdatedList extends ExamsListDates, ProctoringRoomExam {
  done_today: boolean;
}

class _ProctoringCenter extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      error: false,
      errorMsg: "",
      exams_error: false,
      exams_error_msg: "",
      search_text: "",
      loading_room_details: false,
      get_exams_list: false,
      warning: {
        msg: "",
        target: null,
      },
    };
  }

  componentDidMount = () => {
    if (this.props.auth.user_access.room_id.length >= 3) {
      //** GET ROOM DETAILS */

      if (
        this.props.proctoring.room.room_id === "" &&
        this.props.auth.application_session?._id !== undefined
      ) {
        this.setState({ loading_room_details: true });
        this.props.FC_RoomDetails(
          this.props.auth.user_access.room_id,
          this.props.auth.application_session?._id,

          (status: boolean, errorMsg: string) => {
            if (status === true) {
              this.setState({ loading_room_details: false });

              if (
                this.props.auth.application_session?._id !== undefined &&
                this.props.auth.application_session._id !== ""
              ) {
                //** GET EXAMS DETAILS */
                this.setState({ get_exams_list: true });

                this.props.FC_ExamsDetailsData(
                  this.props.auth.application_session?._id,
                  (status: boolean, error: string) => {
                    if (status === false) {
                      this.setState({
                        exams_error: true,
                        exams_error_msg: error,
                        get_exams_list: false,
                      });
                    } else {
                      this.setState({ get_exams_list: false });
                    }
                  }
                );
              } else {
                this.setState({
                  error: true,
                  errorMsg: "No application session found",
                  warning: {
                    msg: errorMsg,
                    target: "main-error",
                  },
                  loading_room_details: false,
                });
                this.clearError();
              }
            } else {
              this.setState({
                error: true,
                errorMsg: errorMsg,
                loading_room_details: false,
                warning: {
                  msg: errorMsg,
                  target: "main-error",
                },
              });
              this.clearError();
            }
          }
        );
      } else {
        this.setState({
          warning: {
            target: "main-error",
            msg: "Failed to load the data",
          },
        });
        this.clearError();
      }
    }
  };

  clearError = () =>
    setTimeout(() => {
      this.setState({
        warning: {
          target: null,
          msg: "",
        },
      });
    }, 2000);

  render() {
    if (this.props.auth.socket_failed === true)
      return (
        <div className=" py-8 mt-2 text-center rounded container mx-auto">
          <MdSignalWifiConnectedNoInternet4
            size={100}
            className="mx-auto text-yellow-500 opacity-70 animate__animated animate__fadeIn animate__infinite animate__slow"
          />

          <h3 className="pt-6 text-xl text-gray-600">
            Your computer is not connected to the <b>Live-server</b>
          </h3>
          <p>PLease refresh your browser, or use incognito mode</p>
        </div>
      );

    if (this.state.error === true) {
      return (
        <div className="mt-4">
          <div className="p-5 bg-red-100 rounded-md mx-auto text-center text-red-900 animate__animated animate__bounceIn">
            <h1 className="text-3xl font-bold">Failed do load data</h1>
            <p>{this.state.errorMsg}</p>
          </div>
        </div>
      );
    }
    if (
      this.state.error === false &&
      this.props.proctoring.room.loaded === false
    ) {
      return (
        <div className="m-5 text-center">
          <h5 className="text-gray-400 animate__animated animate__fadeIn animate__infinite text-xl">
            Loading room details
          </h5>
        </div>
      );
    }
    if (
      this.props.proctoring.exam.course_id !== "" &&
      this.props.proctoring.exam.position_id !== ""
    ) {
      return <ProctoringOnlineSupervisor homeProctoring={false} />;
    }
    const { room_number, center_name, district_name, province_name } =
      this.props.proctoring.room;

    let list_data: UpdatedList[] = [];
    let exam_data: ProctoringRoomExam[] = this.props.proctoring.room_exams;

    for (const iterator of exam_data) {
      let examFound = this.props.proctoring.exams_list.exams.find(
        (exm) => exm.course === iterator.course_id
      );

      if (examFound === undefined) {
        continue;
      } else {
        list_data.push({
          ...examFound,
          ...iterator,
          done_today: isToday(examFound.exam_date),
        });
      }
    }

    let courses_results = search(list_data, this.state.search_text, {
      course_id: true,
      course_name: true,
      course_code: true,
    });
    return (
      <Fragment>
        {this.state.warning.target !== null && (
          <Alert theme={AlertInterface.danger} title={this.state.warning.msg} />
        )}
        <div className="mr-2">
          <div className="p-5 bg-white rounded-b-md ">
            <h2 className="text-2xl text-gray-600 font-bold text-center border-b pb-2">
              Room details
            </h2>
            <div className="flex flex-col md:flex-row">
              <div className="flex flex-1 items-center">
                <div className="p-4">
                  <MdLocationOn className="text-4xl" />
                </div>
                <div className="p-2 pt-3 flex-1">
                  <div className="mb-1">
                    <span className="text-gray-400">Province:</span>{" "}
                    <b> {province_name}</b>
                  </div>
                  <div className="mb-1">
                    <span className="text-gray-400">District:</span>{" "}
                    <b> {district_name}</b>
                  </div>
                </div>
              </div>
              <div className="flex flex-1 items-center">
                <div className="p-4">
                  <FaSchool className="text-4xl" />
                </div>
                <div className="p-2 flex-1">
                  <div className="mb-1">
                    <span className="text-gray-400">Center name:</span>{" "}
                    <b> {center_name}</b>
                  </div>

                  <div className="mb-1">
                    <span className="text-gray-400">Room Number:</span>{" "}
                    <b> {room_number}</b>
                  </div>
                </div>
              </div>
            </div>{" "}
          </div>
          {this.state.loading_room_details === true && (
            <div className="p-2 rounded-md text-xl text-center bg-blue-400 text-white animate__fadeIn animate__animated animate__infinite my-2">
              Loading room details...
            </div>
          )}
          {this.state.get_exams_list === true && (
            <div className="p-2 rounded-md text-xl text-center bg-blue-400 text-white animate__fadeIn animate__animated animate__infinite my-2">
              Loading exams...
            </div>
          )}
          <h2 className="mt-5 text-2xl font-bold text-gray-500">
            Active exams
          </h2>
          <div className="mt-2 p-2 bg-white rounded-md">
            {this.state.get_exams_list === true ? (
              <div className="text-gray-300 text-center">Loading room...</div>
            ) : this.props.proctoring.room_exams.length <= 0 ? (
              <div className="p-5 text-center">
                No exams assigned to this room
              </div>
            ) : (
              <>
                <div className="pt-1 pb-3">
                  <input
                    type="search"
                    onChange={(e) =>
                      this.setState({ search_text: e.target.value })
                    }
                    value={this.state.search_text}
                    className="input bg-gray-100 p-1"
                    placeholder="Search"
                  />
                </div>

                <table className="w-full text-left">
                  <thead className="bg-gray-100 rounded-md">
                    <tr>
                      <th className="p-2">#</th>
                      <th className="p-2">Course</th>
                      <th className="p-2">Position</th>
                      <th className="p-2">Code</th>
                      <th className="p-2">Date</th>
                      <th className="p-2"></th>
                    </tr>
                  </thead>
                  <tbody>
                    {courses_results.length === 0 && (
                      <tr>
                        <td colSpan={7} className="py-6 text-center">
                          <h2 className="text-3xl text-gray-500">
                            No data found
                          </h2>
                        </td>
                      </tr>
                    )}

                    {courses_results
                      .filter((itm: UpdatedList) => itm.done_today === true)
                      .map((room: UpdatedList, key) => {
                        return (
                          <tr
                            className="p-2 border-t group hover:bg-blue-50"
                            key={key}
                          >
                            <td className="p-2 text-gray-400">{key + 1}</td>
                            <td className="p-2 font-bold">
                              {room.course_name}
                            </td>
                            <td className="p-2">
                              <span className="p-1 text-sm bg-yellow-100 font-bold text-yellow-900 rounded">
                                {room.position_title}
                              </span>
                            </td>
                            <td className="p-2">{room.course_code}</td>
                            <td className="p-2 text-sm">
                              {this.state.error === false &&
                              this.props.proctoring.exams_list.loaded ===
                                false ? (
                                <span className="text-blue-500 animate__animated animate__fadeIn animate__infinite">
                                  Loading...
                                </span>
                              ) : (
                                moment(room.exam_date).format("LLLL")
                              )}
                            </td>
                            <td className="p-2 text-right">
                              <Fragment>
                                {room.course_id ===
                                  this.props.proctoring.exam.course_id &&
                                  room.position_id ===
                                    this.props.proctoring.exam.position_id && (
                                    <button
                                      className=" group-hover:bg-red-500 py-1 px-3 rounded text-red-500 group-hover:text-white bg-red-100"
                                      onClick={() =>
                                        window.confirm(
                                          "Are you sure you want to exist the room"
                                        ) &&
                                        this.props.FC_ClearExamToRoomProctoring()
                                      }
                                    >
                                      Leave Room
                                    </button>
                                  )}

                                {this.props.proctoring.exam.room_id === "" && (
                                  <button
                                    onClick={() => {
                                      this.props.FC_LoadExamToRoomProctoring({
                                        video_support: false,
                                        start_exam: false,
                                        start_time: room?.start_time,
                                        end_time: "",
                                        exam_done: false,
                                        period_id:
                                          this.props.auth.application_session
                                            ?._id,
                                        exam_status: room?.exam_status,
                                        exam_date: room?.exam_date,
                                        duration: room?.duration,
                                        position_id: room.position_id,
                                        position_title: room.position_title,
                                        course_id: room.course_id,
                                        course_code: room.course_code,
                                        course_name: room.course_name,
                                        room_id:
                                          this.props.proctoring.room.room_id,
                                        exam_id: room.id,
                                      } as ProctoringExam);
                                      this.props.FC_AddRules(room.rules);
                                    }}
                                    className=" group-hover:bg-blue-500 py-1 px-3 rounded text-blue-500 group-hover:text-white bg-blue-100"
                                  >
                                    Open
                                  </button>
                                )}
                              </Fragment>
                            </td>
                          </tr>
                        );
                      })}
                  </tbody>
                </table>
              </>
            )}
          </div>
        </div>

        <h2 className="mt-5 text-2xl font-bold text-gray-500">
          Others exams on this room
        </h2>
        <div className="p-2 bg-white mt-2 rounded-md">
          <table className="w-full text-left">
            <thead className="bg-gray-100 rounded-md">
              <tr>
                <th className="p-2">#</th>
                <th className="p-2">Course</th>
                <th className="p-2">Position</th>
                <th className="p-2">Code</th>
                <th className="p-2">Date</th>
                <th className="p-2"></th>
              </tr>
            </thead>
            <tbody>
              {courses_results.length === 0 && (
                <tr>
                  <td colSpan={7} className="py-6 text-center">
                    <h2 className="text-3xl text-gray-500">No data found</h2>
                  </td>
                </tr>
              )}

              {courses_results
                .filter((itm: UpdatedList) => itm.done_today === false)
                .map((room: UpdatedList, key) => {
                  return (
                    <tr className="p-2 border-t  hover:bg-blue-50-" key={key}>
                      <td className="p-2 text-gray-400">{key + 1}</td>
                      <td className="p-2 font-bold">{room.course_name}</td>
                      <td className="p-2">
                        <span className="p-1 text-sm bg-yellow-100 font-bold text-yellow-900 rounded">
                          {room.position_title}
                        </span>
                      </td>
                      <td className="p-2">{room.course_code}</td>
                      <td className="p-2 text-sm">
                        {this.state.error === false &&
                        this.props.proctoring.exams_list.loaded === false ? (
                          <span className="text-blue-500 animate__animated animate__fadeIn animate__infinite">
                            Loading...
                          </span>
                        ) : (
                          moment(room.exam_date).format("LLLL")
                        )}
                      </td>
                      <td className="p-2 text-right">{room.exam_status}</td>
                    </tr>
                  );
                })}
            </tbody>
          </table>
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  proctoring,
}: StoreState): { auth: Auth; proctoring: Proctoring } => {
  return { auth, proctoring };
};

export const ProctoringCenter = connect(mapStateToProps, {
  FC_RoomDetails,
  FC_ExamsDetailsData,
  FC_LoadExamToRoomProctoring,
  FC_ClearExamToRoomProctoring,
  FC_AddRules,
})(_ProctoringCenter);
