import React, { FC, ReactElement } from "react";

const HoverControls: FC<{
  icon: any;
  title: string;
  sub_title: string;
  stop: () => void;
  only: () => void;
  add: () => void;
  name: string;
}> = ({ title, stop, only, add, name, icon, sub_title }): ReactElement => {
  const Icon = icon;
  return (
    <div className=" hover-child border border-white border-opacity-40 bg-opacity-90 bg-blue-900 w-52 z-50 p-2 rounded -mt-2">
      <Icon className="text-4xl mb-4 block mx-auto" />
      <button
        onClick={stop}
        className="px-0.5 py-1 hover:bg-red-600 text-center text-sm mb-2 border border-red-300 rounded-md  w-full block bg-red-600 bg-opacity-80"
      >
        <span>
          Stop {title} to <b>{name}</b>
        </span>
      </button>
      <button
        onClick={only}
        className="px-0.5 py-1 hover:bg-green-600 text-center text-sm mb-2 border border-green-300 rounded-md  w-full block bg-green-600 bg-opacity-80"
      >
        <span>
          {title} <b>{name}</b> only
        </span>
      </button>
      <button
        onClick={add}
        className="px-0.5 hover:bg-blue-600 py-1 text-center text-sm border border-blue-300 rounded-md  w-full block bg-blue-800"
      >
        <span>
          Add <b>{name}</b> to {sub_title}
        </span>
      </button>
    </div>
  );
};

export default HoverControls;
