import React, { FC, ReactElement, useState } from "react";
import { CandidateProctoringInterface, EventInterface } from "../../interfaces";
import ProctoringEvents from "../ProctoringEvents/ProctoringEvents";
import {
  MdAccountCircle,
  MdArrowBack,
  MdVolumeUp,
  MdFullscreen,
  MdNotInterested,
  MdReplay,
  MdWarning,
  MdOutlineMic,
  MdMicOff,
  MdVolumeOff,
  MdPhonelinkOff,
  MdOutlineSmartToy,
  MdPlayArrow,
} from "react-icons/md";
import HoverControls from "../VideoCardProctoring/HoverControls";

type ProctoringCandidateDetailsProps = {
  candidate: CandidateProctoringInterface | undefined;
  stopHandler: Function;
  restartHandler: Function;
  warnHandler: Function;
  closeHandler: Function;
  removeInExam: Function;
  events: EventInterface[];
  exam_started: boolean;
  focus_mode: boolean;
  homeProctoring: boolean;
  //
  continueHandler: (user_id: string) => void;
  focusHandler: (user_id: string) => void;
  micAdd: (user_id: string) => void;
  micStop: (user_id: string) => void;
  micOnly: (user_id: string) => void;
  soundAdd: (user_id: string) => void;
  soundStop: (user_id: string) => void;
  soundOnly: (user_id: string) => void;
  resetDevice: (user_id: string) => void;
  setAiProctoring: (user_id: string, status: boolean) => void;
};

enum ActiveTap {
  Details,
  Events,
}

const ProctoringCandidateDetails: FC<ProctoringCandidateDetailsProps> = ({
  candidate,
  focusHandler,
  stopHandler,
  restartHandler,
  warnHandler,
  closeHandler,
  events,
  homeProctoring,
  exam_started,
  focus_mode,
  removeInExam,
  micAdd,
  micStop,
  micOnly,
  soundAdd,
  soundStop,
  soundOnly,
  resetDevice,
  setAiProctoring,
  continueHandler,
}): ReactElement => {
  const [active, setActive] = useState(ActiveTap.Details);

  if (candidate === undefined) {
    return (
      <div className="p-4 text-center bg-white">
        <h1 className="text-3xl text-gray-800">Oops!!</h1>
        <p className="text-3xl text-gray-700">
          The selected candidate can't be found{" "}
        </p>
      </div>
    );
  }

  return (
    <div className="p-1">
      <div
        className={`p-2 border-rounded ${
          focus_mode === true
            ? "bg-blue-900 text-white"
            : "bg-gray-500 text-white"
        }  rounded-md flex items-baseline `}
      >
        <button
          onClick={() => closeHandler()}
          className="p-1 block bg-blue-900 bg-opacity-70 rounded-md mr-2 font-bold text-1xl"
        >
          <MdArrowBack />
        </button>
        <h3 className="text-center -mt-1 mb-0 pb-0 text-1xl font-bold block text-blue-500--">
          User details
        </h3>
      </div>
      <div className="p-2 text-center">
        <MdAccountCircle className="text-9xl text-center mx-auto" />

        <h1
          className={`text-xl  ${
            focus_mode === true ? "text-white" : "text-gray-800"
          } font-bold`}
        >
          {candidate.fname} {candidate.lname}
        </h1>
        <h2
          className={` ${
            focus_mode === true ? "text-gray-400" : "text-gray-800"
          }  text-md`}
        >
          {candidate.email}
        </h2>
        <div className="p-1">
          {candidate.joined === true && (
            <span className="inline-block text-sm bg-blue-500 text-white px-1 rounded-md m-0.5">
              #Joined
            </span>
          )}
          {candidate.active === true && (
            <span className="inline-block text-sm bg-green-500 text-white px-1 rounded-md m-0.5">
              #Active
            </span>
          )}
          {candidate.removed === true && (
            <span className="inline-block text-sm bg-red-500 text-white  px-1 rounded-md m-0.5">
              #Removed
            </span>
          )}
          {candidate.done === true && (
            <span className="inline-block text-sm bg-yellow-500 text-white px-1 rounded-md m-0.5">
              #Done
            </span>
          )}
        </div>
      </div>
      <div className="my-2 py-1 rounded grid grid-cols-3 gap-1">
        {/* buttons start here */}

        {candidate.done === false && (
          <button
            title="Clear device"
            onClick={() => resetDevice(candidate.user_id)}
            className="proctoring-action-button"
          >
            <MdPhonelinkOff className="text-2xl mx-auto b-0 pb-0" />
            <span className="text-sm p-0 block -mt-0">Reset device</span>
          </button>
        )}

        {exam_started === true && candidate.active === true && (
          <>
            {homeProctoring === true && (
              <>
                <div className="inline-block hover-parent">
                  <button className="proctoring-action-button w-full">
                    {candidate.mic === true ? (
                      <MdOutlineMic className="text-red-400 text-2xl mx-auto b-0 pb-0" />
                    ) : (
                      <MdMicOff className="text-gray-100 text-2xl mx-auto b-0 pb-0" />
                    )}
                    <span className="text-sm p-0 block -mt-0">Speak</span>
                  </button>

                  <HoverControls
                    add={micAdd.bind(this, candidate.user_id)}
                    stop={micStop.bind(this, candidate.user_id)}
                    only={micOnly.bind(this, candidate.user_id)}
                    icon={candidate.mic === true ? MdOutlineMic : MdMicOff}
                    name={candidate.lname}
                    sub_title={"speakers"}
                    title={"speak"}
                  />
                </div>

                <div className="inline-block hover-parent">
                  <button className="proctoring-action-button w-full">
                    {candidate.audio === true ? (
                      <MdVolumeUp className="text-green-400 text-2xl mx-auto b-0 pb-0" />
                    ) : (
                      <MdVolumeOff className="text-gray-100 text-2xl mx-auto b-0 pb-0" />
                    )}
                    <span className="text-sm p-0 block -mt-0">Listen</span>
                  </button>

                  <HoverControls
                    add={soundAdd.bind(this, candidate.user_id)}
                    stop={soundStop.bind(this, candidate.user_id)}
                    only={soundOnly.bind(this, candidate.user_id)}
                    icon={candidate.audio === true ? MdVolumeUp : MdVolumeOff}
                    name={candidate.lname}
                    sub_title={"listeners"}
                    title={"listen"}
                  />
                </div>

                <button
                  onClick={focusHandler.bind(this, candidate.user_id)}
                  className="proctoring-action-button"
                >
                  <MdFullscreen className="text-2xl mx-auto b-0 pb-0" />
                  <span className="text-sm p-0 block -mt-0">Focus</span>
                </button>
                {/* 
                <button
                  title="Ai Assistant"
                  onClick={() =>
                    setAiProctoring(candidate.user_id, !candidate.ai)
                  }
                  className="proctoring-action-button hover:bg-green-400"
                >
                  <MdOutlineSmartToy className="text-2xl mx-auto b-0 pb-0" />
                  <span className="text-sm p-0 block -mt-0">Ai Assistant</span>
                </button> */}
              </>
            )}

            {/* <button
              title="Clear device"
              onClick={() => resetDevice(candidate.user_id)}
              className="proctoring-action-button"
            >
              <MdPhonelinkOff className="text-2xl mx-auto b-0 pb-0" />
              <span className="text-sm p-0 block -mt-0">Clear device</span>
            </button> */}

            {candidate.done === false && (
              <>
                {/* <button
                  title="Clear device"
                  onClick={() => resetDevice(candidate.user_id)}
                  className="proctoring-action-button"
                >
                  <MdPhonelinkOff className="text-2xl mx-auto b-0 pb-0" />
                  <span className="text-sm p-0 block -mt-0">Clear device</span>
                </button> */}

                {candidate.stopped === true ? (
                  <button
                    onClick={continueHandler.bind(this, candidate.user_id)}
                    className="text-center rounded-md p-1 border-2 border-yellow-600 hover:bg-yellow-600 text-black  bg-yellow-500"
                    title="Continue exam"
                  >
                    <MdPlayArrow className="text-2xl mx-auto b-0 pb-0" />
                    <span className="text-sm p-0 block font-bold -mt-0">
                      Continue
                    </span>
                  </button>
                ) : (
                  <button
                    onClick={stopHandler.bind(this, candidate.user_id)}
                    className="proctoring-action-button hover:bg-red-400 hover:text-white"
                  >
                    <MdNotInterested className="text-2xl mx-auto b-0 pb-0" />
                    <span className="text-sm p-0 block  font-bold -mt-0">
                      Stop
                    </span>
                  </button>
                )}
              </>
            )}

            <button
              onClick={restartHandler.bind(this, candidate.user_id)}
              className="proctoring-action-button"
            >
              <MdReplay className="text-2xl mx-auto b-0 pb-0" />
              <span className="text-sm p-0 block -mt-0">Restart</span>
            </button>

            {candidate.done === false && (
              <button
                title="give a warning"
                onClick={() => warnHandler(candidate.user_id)}
                className="proctoring-action-button"
              >
                <MdWarning className="text-2xl mx-auto b-0 pb-0" />
                <span className="text-sm p-0 block -mt-0">Warn</span>
              </button>
            )}

            {candidate.done === false && (
              <button
                title="Remove in exam"
                onClick={() => removeInExam(candidate.user_id)}
                className="text-center rounded-md p-1 border-2 border-red-600 hover:bg-red-600 text-red-900 hover:text-white  hover:text-bg-red-500 bg-red-300"
              >
                <MdWarning className="text-2xl mx-auto b-0 pb-0" />
                <span className="text-sm p-0 block -mt-0">Remove in exam</span>
              </button>
            )}
          </>
        )}
      </div>

      <div className="my-1 bg-blue-900 bg-opacity-40 rounded-md p-1 mt-2 grid grid-cols-2 gap-2 text-sm">
        <button
          onClick={() => setActive(ActiveTap.Details)}
          className={`p-1 rounded-md text-white ${
            active === ActiveTap.Details
              ? "bg-blue-600 border-2 border-blue-300"
              : "bg-blue-300"
          }`}
        >
          candidate
        </button>

        <button
          onClick={() => setActive(ActiveTap.Events)}
          className={`p-1 rounded-md text-white ${
            active === ActiveTap.Events
              ? "bg-blue-600 border-2 border-blue-300"
              : "bg-blue-300"
          }`}
        >
          Events
        </button>
      </div>

      {active === ActiveTap.Details ? (
        <div
          className={`mt-2 ${
            focus_mode === true ? "text-white" : "text-gray-900"
          } border-blue-400`}
        >
          <h2 className=" font-bold text-2xl mb-1 opacity-70 text-center border-b">
            User information
          </h2>
          <div className="p-2 rounded-md border   border-opacity-20">
            <div
              className={`border-b  border-opacity-60 py-1 ${
                focus_mode === true ? "border-white" : "border-gray-400"
              }`}
            >
              <label className="text-sm m-0 p-0 opacity-70">Username:</label>
              <h4 className="font-bold text-lg p-0 -mt-1">{candidate.email}</h4>
            </div>
            <div
              className={`border-b  border-opacity-60 py-1 ${
                focus_mode === true ? "border-white" : "border-gray-400"
              }`}
            >
              <label className="text-sm m-0 p-0 opacity-70">First name:</label>
              <h4 className="font-bold text-lg p-0 -mt-1">{candidate.fname}</h4>
            </div>
            <div
              className={`border-b  border-opacity-60 py-1 ${
                focus_mode === true ? "border-white" : "border-gray-400"
              }`}
            >
              <label className="text-sm m-0 p-0 opacity-70">Last name:</label>
              <h4 className="font-bold text-lg p-0 -mt-1">{candidate.lname}</h4>
            </div>
            <div
              className={`border-b  border-opacity-60 py-1 ${
                focus_mode === true ? "border-white" : "border-gray-400"
              }`}
            >
              <label className="text-sm m-0 p-0 opacity-70">email:</label>
              <h4 className="font-bold text-lg p-0 -mt-1">{candidate.email}</h4>
            </div>
            <div
              className={`border-b  border-opacity-60 py-1 ${
                focus_mode === true ? "border-white" : "border-gray-400"
              }`}
            >
              <label className="text-sm m-0 p-0 opacity-70">Position:</label>
              <h4 className="font-bold text-lg p-0 -mt-1">
                {candidate.position_title}
              </h4>
            </div>
            <div
              className={`border-b  border-opacity-60 py-1 ${
                focus_mode === true ? "border-white" : "border-gray-400"
              }`}
            >
              <label className="text-sm m-0 p-0 opacity-70">Course name:</label>
              <h4 className="font-bold text-lg p-0 -mt-1">
                {candidate.course_name}
              </h4>
            </div>
          </div>
        </div>
      ) : (
        <ProctoringEvents
          candidates={[candidate]}
          events={events}
          focus_mode={focus_mode}
          loading={false}
        />
      )}
    </div>
  );
};
export default ProctoringCandidateDetails;
